<template>
	<box-item title="播种面积和产量">
		<div class="module-wrap">
			<div class="info-wrap">
				<ul class="info-ul">
					<li class="info-item">
						<p class="info-name">播种面积</p>
						<p class="info-value">
							<b class="value">{{ totalArea }}</b
							>万亩
						</p>
						<p class="info-juxtapose" v-if="areaFloat">
							同比
							<b class="juxtapose-value"> {{ areaFloat }}% <i class="iconfont icon-up"></i> </b>
						</p>
					</li>
					<li class="info-item">
						<p class="info-name">产量</p>
						<p class="info-value">
							<b class="value">{{ totalYield }}</b
							>万吨
						</p>
						<p class="info-juxtapose" v-if="yieldFloat">
							同比
							<b class="juxtapose-value"> {{ yieldFloat }}% <i class="iconfont icon-up"></i> </b>
						</p>
					</li>
				</ul>
			</div>
			<div class="detail-info-wrap">
				<i class="iconfont icon-xiangshang left" :class="{disabled: carouseActionIndex === 0}" @click="prevCarousel" @mouseover="disabledAutoplay" @mouseout="startAutoplay"></i>
				<i
					class="iconfont icon-fangxiang right"
					:class="{disabled: carouseActionIndex === carouselData.length - 1}"
					@click="nextCarousel"
					@mouseover="disabledAutoplay"
					@mouseout="startAutoplay"
				></i>
				<el-carousel ref="carouselEl" :autoplay="carouselAutoplay" arrow="never" indicator-position="none" @change="carouselChange" :interval="3000">
					<el-carousel-item v-for="(carousel, index) in carouselData" :key="index">
						<ul class="info-ul">
							<li class="info-item" v-for="(info, index) in carousel" :key="index">
								<p class="info-name">{{ info.name }}</p>
								<p class="info-value">
									面积：<i class="value">{{ info.area }}万亩 </i>
								</p>
								<p class="info-value">
									产量：<i class="value">{{ info.cropYield }}万吨 </i>
								</p>
							</li>
						</ul>
					</el-carousel-item>
				</el-carousel>
			</div>
		</div>
	</box-item>
</template>

<script>
import BoxItem from '@/components/common/BoxItem';
export default {
	name: 'SownAreaAndYield',
	props: {},
	data() {
		return {
			totalArea: 141.47,
			areaFloat: null,
			totalYield: 284.7,
			yieldFloat: null,
			carouselData: [
				[
					{name: '宝山区', area: 2.68, cropYield: 4.8},
					{name: '嘉定区', area: 9.18, cropYield: 12.97},
					{name: '青浦区', area: 16.28, cropYield: 40.41}
				],
				[
					{name: '松江区', area: 4.68, cropYield: 9.51},
					{name: '金山区', area: 17.0, cropYield: 35.75},
					{name: '闵行区', area: 4.9, cropYield: 6.0}
				],
				[
					{name: '奉贤区', area: 15.78, cropYield: 29.68},
					{name: '浦东新区', area: 22.71, cropYield: 47.91},
					{name: '崇明区', area: 41.01, cropYield: 79.29}
				]
			],
			carouseActionIndex: 0,
			carouselAutoplay: true
		};
	},
	created() {},
	mounted() {},
	methods: {
		nextCarousel() {
			this.carouselElRef.next();
		},
		prevCarousel() {
			this.carouselElRef.prev();
		},
		carouselChange(index) {
			this.carouseActionIndex = index;
		},
		disabledAutoplay() {
			this.carouselAutoplay = false;
		},
		startAutoplay() {
			this.carouselAutoplay = true;
		}
	},
	computed: {
		carouselElRef() {
			return this.$refs.carouselEl;
		}
	},
	components: {
		BoxItem
	}
};
</script>

<style scoped lang="less">
.module-wrap {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.info-wrap {
	padding-bottom: 15px;
	border-bottom: 1px solid #156d86;
	.info-ul {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 20px;
		.info-item {
			text-align: center;
			.info-name {
				font-size: 16px;
				color: #9dd1d7;
				font-weight: bold;
				height: 25px;
				line-height: 25px;
			}
			.info-value {
				color: #feeb48;
				font-size: 14px;
				height: 40px;
				line-height: 40px;
				margin-top: 6px;
				.value {
					font-size: 30px;
					font-weight: bold;
				}
			}
			.info-juxtapose {
				color: #71ccde;
				height: 24px;
				line-height: 24px;
				margin-top: 5px;
				font-size: 14px;
				.juxtapose-value {
					color: #46f262;
				}
				.iconfont {
					font-size: 14px;
					margin: 0 -2px;
				}
			}
		}
	}
}
.detail-info-wrap {
	padding: 15px 30px;
	position: relative;
	box-sizing: border-box;
	height: 108px;
	overflow: hidden;
	.iconfont {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		font-size: 16px;
		color: #049cad;
		margin: 0;
		cursor: pointer;
		&.disabled {
			/*color: #9dd1d7;*/
		}
		&.left {
			left: 8px;
		}
		&.right {
			right: 8px;
		}
	}
	.info-ul {
		display: flex;
		align-items: center;
		justify-content: space-between;
		.info-item {
			text-align: center;
			.info-name {
				font-size: 16px;
				color: #00f6ff;
				height: 25px;
				line-height: 25px;
				margin-bottom: 12px;
			}
			.info-value {
				font-size: 12px;
				height: 22px;
				line-height: 22px;
				color: #9dd1d7;
				.value {
					color: #feeb48;
				}
			}
		}
	}
}
.info-wrap,
.detail-info-wrap {
	box-sizing: border-box;
	width: 100%;
	max-width: calc(420px * 1.5);
	margin: 0 auto;
}
/* 32:9超宽屏幕适配 */
@media screen and (min-width: 3000px) {
	.detail-info-wrap .info-ul .info-item .info-name {
		font-size: 22px;
		height: 25px;
		line-height: 25px;
	}
	.detail-info-wrap .info-ul .info-item .info-value {
		font-size: 18px;
		height: 26px;
		line-height: 26px;
	}
	.info-wrap .info-ul .info-item .info-name {
		font-size: 22px;
	}
	.info-wrap .info-ul .info-item .info-value {
		font-size: 22px;
	}
	.info-wrap .info-ul .info-item .info-value .value {
		font-size: 36px;
	}
}
</style>



